.header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.dark-mode{
  background-color: black;
}
.dark-mode h1{
  color: white;
}
.SearchBox{
  display: flex;
  flex-direction: row;
  background-color:rgb(233,233,233);
  align-items: center;
  margin-top: 3%;
  margin-bottom: 1.3em;
  padding:0.7%;
  border-radius: 10px;
}

#searchBar{
  width: 100%;
  height: inherit;
  background-color: rgb(233,233,233);
  border: none;
  outline: none;
}

.notes-list{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
}
.noteHeader{
  overflow: hidden;
}
#noteText{
   overflow-wrap: break-word;
}
.container{
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 3%;
  padding-left: 3%;
  min-height: 100vh;
}

.note.new{
  background-color: #67d7cc;
}

textarea,textarea:focus{
  border:none;
  outline:none;
  resize:none;
  background-color: #67d7cc;
}
.saveBtn:hover,.modeBtn:hover{
  cursor: pointer;
  background-color: #ededed;
}
.saveBtn,.modeBtn{
  background-color: #e1e1e1;
  border:none;
  border-radius: 15px;
  /* outline:none; */
  padding-top:5px;
  padding-right:10px;
  padding-bottom: 5px;
  padding-left: 10px;
  box-shadow: none;

}

.note{
  border-radius: 10px;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  padding:1rem;
  min-height: 170px;
  white-space: pre-wrap; /*Keep any extra space that user wants in his/her notes*/
}

.note-footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.delete-icon{
  cursor: pointer;
}